import Promise from 'bluebird';
Promise.config({
    // Enable warnings
    warnings: true,
    // Enable long stack traces
    longStackTraces: true,
    // Enable cancellation
    cancellation: false,
    // Enable monitoring
    monitoring: false
});
import {
    Video,
    Logger
} from 'h265ize-backend';
import chalk from 'chalk';

import Screen, {
    List,
    Item
} from './screen.js';
import Options from './options.js';
import * as util from './utils';
import {
    PassThrough
} from 'stream';

const heAudio = require('h265ize-module-he-audio');
const encoder = require('h265ize-module-encoder');
const normalizer = require('h265ize-module-normalize');
const autocrop = require('h265ize-module-autocrop');
const stats = require('h265ize-module-stats');
const sample = require('h265ize-module-sample');
Logger.stream = new PassThrough();

let video = new Video({
    name: 'test',
    input: {
        path: input
    },
    output: {
        path: output
    },

    // Module order matters, the encoder should usually be last
    modules: [new normalizer(), new heAudio(), new autocrop(), new encoder({
        defaults: {
            video: {
                'c:{POS}': 'libx265',
                'pixel_format': 'yuv420p10le',
                'preset': 'slow'
            }
        }
    }), new stats(), new sample()]
});
let video2 = new Video({
    name: 'test',
    input: {
        path: input
    },
    output: {
        path: output
    },

    // Module order matters, the encoder should usually be last
    modules: [new normalizer(), new heAudio(), new autocrop(), new encoder({
        defaults: {
            video: {
                'c:{POS}': 'libx265',
                'pixel_format': 'yuv420p10le'
            }
        }
    }), new stats(), new sample()]
});

let videos = [video, video2];

let items = videos.reduce((arr, video) => {
    let item = new Item({
        title: video.input.base,
        content: new List(video.modules.reduce((arr, module) => {
            let item = new Item({
                title: module.displayName
            });
            module.once('start', () => item.state = 'active');
            module.once('stop', (err) => {
                if (err) {
                    item.state = 'failed';
                    item.error(err.message);
                } else {
                    item.state = 'completed';
                }
            });
            arr.push(item);
            return arr;
        }, []))
    });
    video.once('start', () => {
        item.state = 'active';
    });
    video.once('stop', (err) => {
        if (err) {
            item.state = 'failed';
            item.error = chalk.red(err.split('\n')[0]);
        } else {
            item.state = 'completed';
        }
    });
    arr.push(item);
    return arr;
}, []);

let list = new List(items);
let screen = new Screen(list, Logger.stream);


