import blessed from 'blessed';
import Package from '../package.json';
import clui from 'clui';
import merge from 'lodash.merge';
import cliSpinners from 'cli-spinners';
import figures from 'figures';
import throttle from 'lodash.throttle';
import chalk from 'chalk';

var reqsPerSec = [10, 12, 3, 7, 12, 9, 23, 10, 9, 19, 16, 18, 12, 12];

const defaults = {
    box: {
        border: {
            type: 'line'
        },
        style: {
            border: {
                fg: '#f0f0f0'
            }
        },
        padding: {
            left: 1,
            right: 1
        }
    }
};

const THROTTLE = 80;

let program = blessed.program();
program.hideCursor();

export default class Screen extends blessed.screen {
    title = Package.name;
    constructor(videoList, logStream) {
        super({
            program,
            fastCSR: true
        });
        this.init(videoList, logStream);
    }
    init(videoList, logStream) {
        let _self = this;
        this.info = blessed.box(merge({}, defaults.box, {
            parent: this,
            bottom: 0,
            left: 0,
            right: 0,
            height: 1,
            content: 'Waiting...',
            border: {
                type: 'bg'
            }
        }));

        this.log = blessed.box(merge({}, defaults.box, {
            parent: this,
            scrollable: true,
            scrollbar: {
                ch: ' ',
                inverse: true
            },
            mouse: true,
            right: 0,
            top: 0,
            bottom: this.info.height,
            width: '70%',
            content: ''
        }));
        this.log.on('scroll', this.render.bind(this));

        this.videoList = blessed.box(merge({}, defaults.box, {
            parent: this,
            scrollable: true,
            scrollbar: {
                ch: ' ',
                inverse: true
            },
            left: 0,
            top: 0,
            bottom: this.info.height,
            width: '30%',
            content: videoList.output
        }));
        videoList.onUpdate = () => {
            _self.videoList.setContent(videoList.output);
            _self.render();
        };

        this.key(['escape', 'q', 'C-c'], function(ch, key) {
            return process.exit(0);
        });
        this.render();

        logStream.on('data', (buffer) => {
            const scrolled = _self.log.getScroll() + _self.log.height;
            const height = _self.log.getScrollHeight();
            _self.log.pushLine(buffer.toString('utf8'));
            if (scrolled >= height) {
                _self.log.setScrollPerc(100);
            }
            _self.render();
        });
    }
}

export class Item {
    _state = 'pending'
    _error = ''
    output = ''
    onUpdate = () => {}
    constructor(options) {
        Object.assign(this, options);
        this.render();
    }
    set state(state) {
        this._state = state;
        if (state === 'active') {
            this.interval = setInterval(this.render.bind(this), 80);
        } else {
            if (this.interval)
                clearInterval(this.interval);
        }
        this.render();
    }
    get state() {
        return this._state;
    }
    set error(err) {
        this._error = err;
        this.render();
    }
    render = throttle(() => {
        this.output = `${this.icon} ${this.title} ${this._error}`;
        if (this.content && (this._state === 'active' || this._state === 'failed')) {
            this.output += '\n   ' + this.content.output.replace(/\n/g, '\n   ');
        }
        this.onUpdate();
    }, THROTTLE)
    iconFrame = 0;
    get icon() {
        let icon = '';
        if (this.iconFrame >= cliSpinners.dots.frames.length)
            this.iconFrame = 0;
        switch (this._state) {
            case 'active':
                icon = chalk.blue(cliSpinners.dots.frames[this.iconFrame++]);
                break;
            case 'completed':
                icon = figures.tick;
                break;
            case 'failed':
                icon = figures.cross;
                break;
            default:
                icon = '-';
                break;
        }
        return `${icon} `;
    }
}

export class List {
    output = ''
    onUpdate = () => {}
    constructor(items) {
        this.items = items;
        this.render();
    }
    render = throttle(() => {
        let _self = this;
        this.output = this.items.reduce((arr, item) => {
            item.onUpdate = () => {
                _self.render();
            };
            arr.push(item.output);
            return arr;
        }, []).join('\n');
        this.onUpdate();
    }, THROTTLE);
}



